@media (max-width: 1200px) {
    .menu {
        .menu_item {
            padding-right: 20px; }
        .menu_link {
            font-size: 12px; } }
    .promo {
        .promo_header {
            font-size: 50px; } }
    .reason {
        .reason_descr {
            width: 300px; }
        .round_wrapper {
            align-items: flex-start; } }
    .mobile_item {
        width: 300px; }
    .footer_app {
        img {
            height: 20px; } }
    .social {
        .social_wrapper {
            .social_round {
                height: 30px;
                width: 30px; } } } }

@media (max-width: 991px) {
    .menu {
        .menu_item {
            padding-right: 8px; }
        .menu_link {
            font-size: 10px; } }
    .subheader_logo > img {
        width: 100%; }
    .subheader {
        .subheader_official {
            font-size: 11px; }
        .subheader_phone {
            font-size: 18px; } }
    .subheader_btn {
        width: 100px;
        height: 40%;
        margin-top: 21px; }
    .promo {
        .promo_header {
            font-size: 40px; }
        .promo_subheader {
            font-size: 28px; }
        .promo_descr {
            font-size: 19px; } }
    .label_header {
        font-size: 30px; }
    .reason {
        .reason_descr {
            width: 300px;
            margin-top: 20px; }
        .round_wrapper {
            flex-direction: column;
            align-items: center; } }
    .label_header_car {
        font-size: 30px; }
    .social {
        .social_wrapper {
            width: 120px;
            .social_round {
                height: 25px;
                width: 25px; } } }
    .footer_app {
        bottom: 94px;
        img {
            height: 20px;
            width: 150px; } }

    .mobile_item {
        width: 230px; }
    .mobile_item_subtetle {
        font-size: 18px; } }

@media (max-width: 767px) {
    .menu {
        position: fixed;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        top: 0;
        left: -100%;
        width: 50%;
        height: 100vh;
        padding: 20px 15px;
        background-color: rgba(0, 0, 0, .8);
        transition: 0.6s all;
        &_active {
            left: 0; }
        .menu_item {
            padding-right: 0;
            border: none;
            margin-top: 10px; }
        .menu_link {
            font-size: 20px; } }
    .subheader {
        .xs-hidden {
            display: none; }
        .xs-visible {
            display: block; }
        .subheader_phone {
            display: block;
            margin-top: 10px;
            font-weight: 400;
            font-size: 16px;
            text-transform: uppercase;
            color: #FFFFFF;
            text-align: center; }
        .subheader_btn {
            display: block;
            width: 200px;
            height: 40px;
            margin: 0 auto;
            margin-top: 12px; } }
    .mobile_item {
        width: 350px;
        height: 300px; }
    .mobile_item_subtetle {
        font-size: 18px; }
    .label_header {
        font-size: 20px; }
    .label_subheader {
        font-size: 16px; }
    .down_menu {
        flex-wrap: wrap;
        .down_link {
            font-size: 14px; } }
    .footer_menu {
        .footer_link {
            font-size: 17px; } }
    footer {
        .lang {
            font-size: 14px; } }
    .social {
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        width: 180px;
        margin-top: 180px;
        .social_wrapper {
            width: 180px;
            .social_round {
                height: 40px;
                width: 40px; } } }
    .footer_app {
        right: 50%;
        transform: translateX(50%);
        bottom: -120px;
        img {
            height: 35px;
            width: 344px; } }
    .hamburger {
        display: block;
        position: absolute;
        right: 10px;
        top: 50%;
        transform: translateY(-50%);
        width: 26px;
        height: 12px;
        span {
            display: block;
            height: 2px;
            width: 100%;
            background-color: #fdfdfd;
            margin-bottom: 2px;
            transition: 0.5s all; }
        &_active {
            span {
                margin-bottom: -2px;
                &:nth-child(1) {
                    transform: translateY(3px) rotate(-45deg); }
                &:nth-child(2) {
                    display: none; }
                &:nth-child(3) {
                    transform: translateY(3px) rotate(45deg); } } } } }

@media (max-width: 575px) {
    .subheader {
        height: 60px;
        .subheader_logo {
            margin-top: 18px; }
        .subheader_official {
            display: none; }
        .subheader_phone {
            display: block;
            margin-top: 11px;
            font-weight: 400;
            font-size: 12px;
            text-transform: uppercase;
            color: #FFFFFF;
            text-align: center; }
        .subheader_btn {
            display: block;
            margin: 0 auto;
            margin-top: 5px;
            width: 110px;
            height: 22px;
            background: linear-gradient(0deg, #1EACC7 20%, #4CE2FF 100%);
            border: 1px solid rgba(0, 0, 0, 0.07);
            box-shadow: inset 0px -1px 0px 1px rgba(0, 0, 0, 0.12);
            border-radius: 4px;
            font-weight: 400;
            font-size: 10px;
            text-transform: uppercase;
            color: #FFFFFF; } }
    .promo {
        min-height: 400px;
        padding: 93px 0 35px 0;
        .promo_descr {
            display: none; }
        .promo_header {
            font-size: 30px;
            padding: 0 24px;
            line-height: 35px; }
        .promo_subheader {
            font-size: 20px;
            margin-top: 33px;
            padding: 0 10px;
            line-height: 30px; }
        .promo_btn {
            width: 210px;
            height: 50px;
            background: linear-gradient(0deg, #1EACC7 20%, #4CE2FF 100%);
            box-shadow: inset 0px -4px 0px rgba(0, 0, 0, 0.12);
            border-radius: 4px;
            margin-top: 72px;
            font-weight: 400;
            font-size: 16px;
            text-transform: uppercase;
            color: #FFFFFF;
            text-shadow: 0px -2px 1px rgba(0, 0, 0, 0.12);
            line-height: 2px; } }
    .menu {
        width: 85%;
        .menu_item {
            width: 100%;
            text-align: center;
            padding: 5px 0; }
        .menu_link {
            display: block; } }
    .reason {
        padding: 22px 0 36px 0; }
    .car {
        height: 560px; }
    .mobile_item {
        width: 280px;
        height: 260px; }
    .footer_menu {
        padding-left: 0;
        justify-content: space-between;
        .footer_item {
            padding-right: 0; } }
    .social {
        left: 50%;
        transform: translateX(-50%);
        margin-top: 200px; }

    .footer_app {
        right: 50%;
        transform: translateX(50%);
        bottom: -130px; }
    .down_menu {
        .down_item {
            padding-right: 25px; } } }

