.mobile {
  min-height: 864px;
  padding: 48px 0 79px 0;
  background: url(../img/second_screen.jpg) center center/cover no-repeat;
  .row {
    margin-top: 32px; }
  &_item {
    position: relative;
    width: 320px;
    height: 280px;
    margin: 0 auto;
    margin-bottom: 19px;
    padding-top: 92px;
    &_1 {
      background: url(../img/pic1.png) center center/cover no-repeat; }
    &_2 {
      background: url(../img/pic2.png) center center/cover no-repeat; }
    &_3 {
      background: url(../img/pic3.png) center center/cover no-repeat; }
    &_4 {
      background: url(../img/pic4.png) center center/cover no-repeat; }
    &_5 {
      background: url(../img/pic5.png) center center/cover no-repeat; }
    &_6 {
      background: url(../img/pic6.png) center center/cover no-repeat; }
    &_subtetle {
      font-weight: 400;
      font-size: 24px;
      line-height: 31px;
      text-align: center;
      text-transform: uppercase;
      color: #FFFFFF; }
    &_plus {
      display: flex;
      justify-content: center;
      align-items: center;
      position: absolute;
      bottom: 35px;
      left: 50%;
      transform: translateX(-50%);
      width: 29px;
      height: 29px;
      border-radius: 100%;
      background: #000000;
      margin: 0 auto;
      padding: 6px;
      cursor: pointer;
      &::before {
        content: '';
        display: block;
        width: 14px;
        height: 2px;
        background-color: $main_color;
        transition: 0.3s all; }
      &::after {
        content: '';
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        display: block;
        height: 14px;
        width: 2px;
        background-color: $main_color;
        transition: 0.3s all; }
      &:hover {
        &::before, &::after {
          background-color: $second_color; } } } } }

.car {
  padding: 50px 0 137px 0;
  min-height: 898px;
  position: relative;
  background: url('../img/second_screen.jpg') center (center / cover) no-repeat;
  &_descr {
