  &_image {
    display: block;
    position: relative;
    margin: 0 auto;
    margin-top: 54px;
    z-index: 2;
    width: 100%; } }

.label_btn_car {
  display: block;
  position: relative;
  margin: 0 auto;
  width: 115px;
  height: 27px;
  background: #fff;
  font-weight: 400;
  font-size: 11px;
  text-align: center;
  text-transform: uppercase;
  color: #000;
  mix-blend-mode: normal;
