.black {
  position: absolute;
  top: 0;
  left: 0;
  height: 499px;
  width: 100%;
  background-color: #070716;
  z-index: 1; }

.require {
  padding: 68px 0 51px 0;
  &_block {
    margin: 33px 0; }
  &_title {}
  font-weight: 400;
  font-size: 22px;
  color: #1CA8C3;
  &_list {
    list-style-type: none;
    padding-left: 25px;
    margin-top: 22px;
    li {
      font-weight: 300;
      font-size: 17px;
      line-height: 24px;
      color: #222222;
      margin-bottom: 10px;
      &::before {
        content: '';
        display: inline-block;
        vertical-align: middle;
        width: 21px;
        height: 20px;
        background: url('../icons/blue.png') center center/cover no-repeat;
        transform: translate(-5px , -2px); } } }
  &_descr {
    font-weight: 300;
    font-size: 17px;
    color: #1CA8C3; } }

.warning {
    .require_title {
      color: $second-color; }
    .require_list {
      li {
        &::before {
          background: url('../icons/red-1.png') center center/cover no-repeat; } } } }

